// =============================================================================
// API Client for Dashboard
// =============================================================================
// Client-side utilities for calling the API from React components

// Use relative URLs when running on same domain, absolute when different
const API_BASE_URL = typeof window !== 'undefined' 
  ? (process.env.NEXT_PUBLIC_API_URL || '') // Empty string = same origin
  : 'http://localhost:3001';

export class ApiError extends Error {
  constructor(
    public status: number,
    public code: string,
    message: string,
    public details?: any
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

async function apiRequest<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const apiKey = typeof window !== 'undefined' ? localStorage.getItem('api_key') : null;
  
  if (!apiKey) {
    throw new ApiError(401, 'UNAUTHORIZED', 'API key not found. Please set your API key in settings.');
  }

  // Use relative URL when in browser (same origin), absolute when in server
  const url = typeof window !== 'undefined' 
    ? endpoint  // Relative URL - same origin
    : `${API_BASE_URL}${endpoint}`;  // Absolute URL for server-side

  const response = await fetch(url, {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      'x-api-key': apiKey, // Use x-api-key header (standard for NexFlow)
      ...options.headers,
    },
  });

  const data = await response.json().catch(() => ({}));

  if (!response.ok) {
    throw new ApiError(
      response.status,
      data.code || 'UNKNOWN_ERROR',
      data.error || data.message || `Request failed with status ${response.status}`,
      data.details
    );
  }

  return data;
}

// Endpoints API
export const endpointsApi = {
  list: () => apiRequest<{ success: boolean; endpoints: any[] }>('/api/v1/endpoints'),
  get: (id: string) => apiRequest<{ success: boolean; endpoint: any }>(`/api/v1/endpoints/${id}`),
  create: (data: {
    name: string;
    description?: string;
    upstreamUrl: string;
    price: string;
    network?: string;
    tokenAddress?: string;
    tokenSymbol?: string;
    chainId?: number;
    recipientAddress: string;
  }) => apiRequest<{ success: boolean; endpoint: any }>('/api/v1/endpoints', {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  update: (id: string, data: Partial<any>) => apiRequest<{ success: boolean; endpoint: any }>(`/api/v1/endpoints/${id}`, {
    method: 'PATCH',
    body: JSON.stringify(data),
  }),
  delete: (id: string) => apiRequest<{ success: boolean }>(`/api/v1/endpoints/${id}`, {
    method: 'DELETE',
  }),
  getStats: (id: string) => apiRequest<{ success: boolean; stats: any; recentPayments: any[] }>(`/api/v1/endpoints/${id}/stats`),
};

// API Keys API
export const apiKeysApi = {
  list: () => apiRequest<{ success: boolean; apiKeys: any[] }>('/api/v1/auth/keys'),
  create: (data: { name: string; role?: string; rateLimit?: number }) => 
    apiRequest<{ success: boolean; apiKey: any; token: string }>('/api/v1/auth/keys', {
      method: 'POST',
      body: JSON.stringify(data),
    }),
  revoke: (id: string) => apiRequest<{ success: boolean }>(`/api/v1/auth/keys/${id}`, {
    method: 'DELETE',
  }),
};

// Analytics API
export const analyticsApi = {
  getEndpointAnalytics: (endpointId: string, params?: {
    startDate?: string;
    endDate?: string;
    granularity?: 'hour' | 'day' | 'week';
    includeDetails?: boolean;
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.startDate) searchParams.set('startDate', params.startDate);
    if (params?.endDate) searchParams.set('endDate', params.endDate);
    if (params?.granularity) searchParams.set('granularity', params.granularity);
    if (params?.includeDetails) searchParams.set('includeDetails', 'true');
    const query = searchParams.toString();
    return apiRequest<any>(`/api/v1/metered/${endpointId}/analytics${query ? `?${query}` : ''}`);
  },
};

// Billing API
export const billingApi = {
  getUsage: (params?: {
    startDate?: string;
    endDate?: string;
    periodType?: 'daily' | 'weekly' | 'monthly';
    customerId?: string;
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.startDate) searchParams.set('startDate', params.startDate);
    if (params?.endDate) searchParams.set('endDate', params.endDate);
    if (params?.periodType) searchParams.set('periodType', params.periodType);
    if (params?.customerId) searchParams.set('customerId', params.customerId);
    const query = searchParams.toString();
    return apiRequest<any>(`/api/v1/billing/usage${query ? `?${query}` : ''}`);
  },
  getReport: (params?: {
    startDate?: string;
    endDate?: string;
    periodType?: 'daily' | 'weekly' | 'monthly';
    endpointIds?: string[];
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.startDate) searchParams.set('startDate', params.startDate);
    if (params?.endDate) searchParams.set('endDate', params.endDate);
    if (params?.periodType) searchParams.set('periodType', params.periodType);
    if (params?.endpointIds) searchParams.set('endpointIds', params.endpointIds.join(','));
    const query = searchParams.toString();
    return apiRequest<any>(`/api/v1/billing/report${query ? `?${query}` : ''}`);
  },
  getInvoice: (params?: {
    startDate?: string;
    endDate?: string;
    periodType?: 'daily' | 'weekly' | 'monthly';
    customerId?: string;
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.startDate) searchParams.set('startDate', params.startDate);
    if (params?.endDate) searchParams.set('endDate', params.endDate);
    if (params?.periodType) searchParams.set('periodType', params.periodType);
    if (params?.customerId) searchParams.set('customerId', params.customerId);
    const query = searchParams.toString();
    return apiRequest<any>(`/api/v1/billing/invoice${query ? `?${query}` : ''}`);
  },
};

// Monitoring API
export const monitoringApi = {
  getMetrics: (params?: {
    name?: string;
    startTime?: number;
    endTime?: number;
    tags?: Record<string, string>;
    recent?: boolean;
    limit?: number;
  }) => {
    const searchParams = new URLSearchParams();
    if (params?.name) searchParams.set('name', params.name);
    if (params?.startTime) searchParams.set('startTime', params.startTime.toString());
    if (params?.endTime) searchParams.set('endTime', params.endTime.toString());
    if (params?.tags) searchParams.set('tags', JSON.stringify(params.tags));
    if (params?.recent) searchParams.set('recent', 'true');
    if (params?.limit) searchParams.set('limit', params.limit.toString());
    const query = searchParams.toString();
    return apiRequest<any>(`/api/v1/monitoring/metrics${query ? `?${query}` : ''}`);
  },
  getAlerts: (includeRules?: boolean) => {
    const searchParams = new URLSearchParams();
    if (includeRules !== false) searchParams.set('includeRules', 'true');
    return apiRequest<any>(`/api/v1/monitoring/alerts?${searchParams.toString()}`);
  },
  createAlertRule: (data: {
    name: string;
    metricName: string;
    condition: 'gt' | 'gte' | 'lt' | 'lte' | 'eq';
    threshold: number;
    windowMs: number;
    tags?: Record<string, string>;
    enabled?: boolean;
    severity?: 'info' | 'warning' | 'error' | 'critical';
  }) => apiRequest<any>('/api/v1/monitoring/alerts', {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  deleteAlertRule: (ruleId: string) => apiRequest<{ success: boolean }>(`/api/v1/monitoring/alerts/${ruleId}`, {
    method: 'DELETE',
  }),
};

// Webhooks API
export const webhooksApi = {
  list: (endpointId?: string) => {
    const searchParams = new URLSearchParams();
    if (endpointId) searchParams.set('endpointId', endpointId);
    const query = searchParams.toString();
    return apiRequest<{ webhooks: any[]; count: number }>(`/api/v1/webhooks${query ? `?${query}` : ''}`);
  },
  create: (data: {
    url: string;
    events: string[];
    endpointId?: string;
    enabled?: boolean;
  }) => apiRequest<any>('/api/v1/webhooks', {
    method: 'POST',
    body: JSON.stringify(data),
  }),
  update: (id: string, data: {
    url?: string;
    events?: string[];
    enabled?: boolean;
  }) => apiRequest<any>(`/api/v1/webhooks/${id}`, {
    method: 'PATCH',
    body: JSON.stringify(data),
  }),
  delete: (id: string) => apiRequest<{ success: boolean }>(`/api/v1/webhooks/${id}`, {
    method: 'DELETE',
  }),
};

// Health check
export const healthApi = {
  check: () => apiRequest<{ healthy: boolean }>('/api/x402/health'),
};

