'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { 
  Server, 
  Plus,
  AlertCircle,
  ExternalLink
} from 'lucide-react'
import { endpointsApi, ApiError } from '@/lib/api-client'

export default function DashboardPage() {
  const [endpoints, setEndpoints] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [])

  const loadData = async () => {
    try {
      setLoading(true)
      setError(null)
      const response = await endpointsApi.list()
      setEndpoints(response.endpoints || [])
    } catch (err) {
      console.error('Load data error:', err)
      if (err instanceof ApiError) {
        // Show detailed error message
        const errorMsg = err.status === 401 
          ? 'Invalid API key. Please check your API key and try again.'
          : err.status === 403
          ? 'Access denied. Your API key may not have the required permissions.'
          : `${err.message} (Status: ${err.status}, Code: ${err.code})`
        setError(errorMsg)
      } else if (err instanceof Error) {
        // Network errors, etc.
        if (err.message.includes('Failed to fetch') || err.message.includes('NetworkError')) {
          setError('Cannot connect to server. Make sure the server is running on http://localhost:3001')
        } else {
          setError(`Failed to load data: ${err.message}`)
        }
      } else {
        setError('Failed to load data: Unknown error')
      }
    } finally {
      setLoading(false)
    }
  }

  return (
    <div>

      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold">Endpoints</h1>
        <Link
          href="/x402-demo"
          className="flex items-center gap-2 px-4 py-2 bg-cyan-500 hover:bg-cyan-600 text-white rounded-lg font-medium transition-colors"
        >
          <ExternalLink className="w-4 h-4" />
          Try x402 Demo
        </Link>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {loading ? (
        <div className="text-center py-12">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
          <p className="mt-4 text-gray-400">Loading...</p>
        </div>
      ) : (
        <EndpointsTab endpoints={endpoints} onRefresh={loadData} />
      )}
    </div>
  )
}

// Endpoints Tab Component
function EndpointsTab({ endpoints, onRefresh }: { endpoints: any[]; onRefresh: () => void }) {
  const [showCreate, setShowCreate] = useState(false)

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold">Metered Endpoints</h2>
        <button
          onClick={() => setShowCreate(true)}
          className="flex items-center gap-2 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
        >
          <Plus className="w-4 h-4" />
          Create Endpoint
        </button>
      </div>

      {showCreate && (
        <CreateEndpointModal
          onClose={() => {
            setShowCreate(false)
            onRefresh()
          }}
        />
      )}

      {endpoints.length === 0 ? (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <Server className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400 mb-4">No endpoints yet</p>
          <button
            onClick={() => setShowCreate(true)}
            className="px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Create Your First Endpoint
          </button>
        </div>
      ) : (
        <div className="grid gap-4">
          {endpoints.map((endpoint) => (
            <EndpointCard key={endpoint.id} endpoint={endpoint} onRefresh={onRefresh} />
          ))}
        </div>
      )}
    </div>
  )
}

// Endpoint Card Component
function EndpointCard({ endpoint, onRefresh }: { endpoint: any; onRefresh: () => void }) {
  const [showDelete, setShowDelete] = useState(false)

  const handleDelete = async () => {
    try {
      await endpointsApi.delete(endpoint.id)
      onRefresh()
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to delete endpoint')
    }
  }

  return (
    <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
      <div className="flex items-start justify-between">
        <div className="flex-1">
          <div className="flex items-center gap-3 mb-2">
            <h3 className="text-xl font-semibold">{endpoint.name}</h3>
            <span className={`px-2 py-1 text-xs rounded ${
              endpoint.status === 'active' 
                ? 'bg-green-900/30 text-green-400' 
                : 'bg-gray-800 text-gray-400'
            }`}>
              {endpoint.status}
            </span>
          </div>
          {endpoint.description && (
            <p className="text-gray-400 mb-4">{endpoint.description}</p>
          )}
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <span className="text-gray-500">Upstream URL:</span>
              <p className="text-gray-300 font-mono text-xs mt-1">{endpoint.upstreamUrl}</p>
            </div>
            <div>
              <span className="text-gray-500">Price:</span>
              <p className="text-gray-300 mt-1">{endpoint.price} {endpoint.tokenSymbol || 'USDC'}</p>
            </div>
            <div>
              <span className="text-gray-500">Network:</span>
              <p className="text-gray-300 mt-1 capitalize">{endpoint.network}</p>
            </div>
            <div>
              <span className="text-gray-500">Metered URL:</span>
              <p className="text-gray-300 font-mono text-xs mt-1 break-all">
                /api/v1/metered/{endpoint.id}
              </p>
            </div>
          </div>
        </div>
        <div className="flex gap-2 ml-4">
          <Link
            href={`/dashboard/endpoints/${endpoint.id}`}
            className="px-3 py-1.5 text-sm bg-gray-800 text-gray-300 rounded hover:bg-gray-700 transition-colors"
          >
            View
          </Link>
          <button
            onClick={() => setShowDelete(true)}
            className="px-3 py-1.5 text-sm bg-red-900/20 text-red-400 rounded hover:bg-red-900/30 transition-colors"
          >
            Delete
          </button>
        </div>
      </div>

      {showDelete && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-md w-full mx-4">
            <h3 className="text-xl font-bold mb-4">Delete Endpoint?</h3>
            <p className="text-gray-400 mb-6">
              Are you sure you want to delete "{endpoint.name}"? This action cannot be undone.
            </p>
            <div className="flex gap-3">
              <button
                onClick={handleDelete}
                className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
              >
                Delete
              </button>
              <button
                onClick={() => setShowDelete(false)}
                className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}


// Create Endpoint Modal
function CreateEndpointModal({ onClose }: { onClose: () => void }) {
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    upstreamUrl: '',
    price: '',
    recipientAddress: '',
    network: 'base',
    tokenSymbol: 'USDC',
    tokenAddress: '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913',
    chainId: 8453,
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      await endpointsApi.create(formData)
      onClose()
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to create endpoint')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
        <h3 className="text-2xl font-bold mb-6">Create Metered Endpoint</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Name *
            </label>
            <input
              type="text"
              required
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Description
            </label>
            <textarea
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
              rows={3}
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Upstream URL *
            </label>
            <input
              type="url"
              required
              value={formData.upstreamUrl}
              onChange={(e) => setFormData({ ...formData, upstreamUrl: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
              placeholder="https://api.example.com/v1/endpoint"
            />
          </div>
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-300 mb-2">
                Price (in wei) *
              </label>
              <input
                type="text"
                required
                value={formData.price}
                onChange={(e) => setFormData({ ...formData, price: e.target.value })}
                className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
                placeholder="1000000"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-300 mb-2">
                Recipient Address *
              </label>
              <input
                type="text"
                required
                value={formData.recipientAddress}
                onChange={(e) => setFormData({ ...formData, recipientAddress: e.target.value })}
                className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
                placeholder="0x..."
              />
            </div>
          </div>
          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors disabled:opacity-50"
            >
              {loading ? 'Creating...' : 'Create Endpoint'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}


